import React from "react"
import { Content } from "./style";

import { Carousel } from 'react-carousel-minimal';

const Stepper = () => {

  const data = [
    {
      image: "https://i.ibb.co/FVf0mVM/1.jpg",
      caption: ""
    },
    {
      image: "https://i.ibb.co/1Gnhpk2/2.jpg",
      caption: "Planejamento Financeiro"
    },
    {
      image: "https://i.ibb.co/B3rH1MP/3.jpg",
      caption: "Gesto Patrimonial"
    },
    {
      image: "https://i.ibb.co/30Vbjkt/6.jpg",
      caption: "Consultoria em TI & Gesto"
    }
  ];

  const captionStyle = {
    fontSize: '2em',
    fontWeight: 'bold',
  }
  const slideNumberStyle = {
    fontSize: '20px',
    fontWeight: 'bold',
  }
  
    return (
      <div className="App">
      <div style={{ textAlign: "center" }}>
        <Content><h2 style={{ color: "SteelBlue" }}>Plantando Atitudes para Colher SUCESSO!!!</h2></Content>
        <div style={{
          padding: "0 20px"
        }}>
          <Carousel
            data={data}
            time={3000}
            width="850px"
            height="500px"
            captionStyle={captionStyle}
            radius="10px"
            slideNumber={true}
            slideNumberStyle={slideNumberStyle}
            captionPosition="bottom"
            automatic={true}
            dots={true}
            pauseIconColor="white"
            pauseIconSize="40px"
            slideBackgroundColor="darkgrey"
            slideImageFit="cover"
            thumbnails={true}
            thumbnailWidth="100px"
            style={{
              textAlign: "center",
              maxWidth: "850px",
              maxHeight: "500px",
              margin: "40px auto",
            }}
          />
        </div>
      </div>
    </div>
    );
  }
  
  export default Stepper;